
import React, { Component } from 'react';
import { Button, Popover } from 'antd';
import {Image, Text, StyleSheet} from 'react';

class Teeth extends Component {
  state = {
    index : this.props.index,
    loc : this.props.loc,
    clicked : false
  };

  convertIntoContent = (info,application) => {

    let ret = [<div><b>Diş Durumu</b></div>]
    if(info.length > 0){
      for (let index = 0; index < info.length; index++) {
        ret.push(<div>{info[index]}</div>)
      }
    }else{
      ret.push([<div>Sağlıklı Diş</div>])
    }
    ret.push([<div><br/><b>Uygulanan İşlemler</b></div>])
    if(application.length > 0){
      for (let index = 0; index < application.length; index++) {
        ret.push(<div>{application[index]}</div>)
      }
    }else{
      ret.push([<div>Yok</div>])
    }
    return ret
  }

  changeType = (info) => {
    if(info.length > 0){
      for (let index = 0; index < info.length; index++) {
        if(info[index]== "Diş Yok"){
          return ""
        }
      }
      return "danger"
    }else{
      return "primary"
    }
  }

  render() {
    return(
      <Popover content={this.convertIntoContent(this.props.obj.info,this.props.obj.application)} title={""+ this.props.loc +this.state.index}>
        <Button type={this.changeType(this.props.obj.info)} disabled={!this.props.obj.isActive} onClick={ () => this.props.click(this.state.index,this.state.loc)}>{""+ this.props.loc +this.state.index}</Button>
      </Popover>
    );
  }
}


export default Teeth;